import { Component } from "react";
import { Alert, Button, Card, Col, Container, Dropdown, DropdownButton, Form, InputGroup, Row, Table } from "react-bootstrap";
import { Loader } from "../CommonComponents/Loader";
import { connect } from "react-redux";
import withRouter from "../../../routes/WithRouter";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import ProfileImage from "../CommonComponents/ProfileImage/ProfileImage";
import { formatTemplateText, SWAL_FIRE, TOAST_ERROR, validateEmail } from "../../../../utils";
import { addCollaborator, getCollaborator } from "../../../../actions/collaboratorActions";
import queryString from 'query-string';
import { COMPANY_NAME } from "../../../../actions/api";
import TabHeading from "../AccountInfo/TabHeading";

const publishHeadings = [{ name: "Website", type: "website" }, { name: "Email Campaigns", type: "email_camp" }, { name: "Shopon.bio", type: "shopon" }]

const featureHeadings = [{ name: "Analytics", type: "analytics" }, { name: "Apps & more", type: "apps_more" }]

class ManageCollaborator extends Component {
    state = {
        isAdmin: true,
        emailId: "",
        userType: 1,
        createGallery: false,
        viewGallery: [],
        editGallery: [],
        website: 0,
        email_camp: 0,
        manageAccount: 0,
        manageFeed: 0,
        analytics: 1,
        isEmailError: false,
        loading: true,
        isChanged: false,
        isEdit: false,
        isSuperAdmin: false,
        shopon: 0,
        apps_more: 0,
        product_catalog: 0
    }

    componentDidMount() {
        this.checkEditInvite()
    }

    checkEditInvite = () => {
        const { userData, params } = this.props
        if (params && Object.keys(params).length > 0 && params.id) {
            getCollaborator(params.id).then(response => {
                const { responseData } = response.data
                this.getCollaboratorData(responseData)
            }).catch(ex => this.setState({ loading: false }, () => { }))
        } else this.setState({ isAdmin: userData && (userData.collaborator == '' || userData.collaborator.activeAccount == "0") ? true : false, loading: false })
    }

    getCollaboratorData = (data) => {
        const { allWalls } = this.props
        const parsed = queryString.parse(window.location.search);
        const { networkManager, otherSettings, publish, contentGallery, product_catalog } = data.permissions
        data.role == 1 ? this.setState({ isAdmin: Object.keys(parsed).length > 0 ? false : true, emailId: data['UserCollab.emailId'], loading: false, isEdit: true }) :
            this.setState({
                isAdmin: false,
                emailId: data['UserCollab.emailId'],
                createGallery: contentGallery.createGallery,

                viewGallery: contentGallery.viewGallery.includes("0") ? allWalls.map(item => item.id) : allWalls
                    .filter(item => contentGallery.viewGallery.includes(String(item.id)))
                    .map(item => item.id),

                editGallery: contentGallery.editGallery.includes("0") ? allWalls.map(item => item.id) : allWalls
                    .filter(item => contentGallery.editGallery.includes(String(item.id)))
                    .map(item => item.id),

                email_camp: publish.email_camp,
                website: publish.website,
                shopon: publish.shopon,
                manageAccount: networkManager.manageAccount,
                manageFeed: networkManager.manageFeed,
                apps_more: otherSettings.apps_more,
                analytics: otherSettings.analytics,
                product_catalog: product_catalog,
                loading: false,
                isEdit: true
            })
    }

    onChangeMailId = event => { this.setState({ emailId: event.target.value, isChanged: true, isEmailError: false }) }

    onSelectGallery = (gallery, action) => {
        let { viewGallery, editGallery, galleryList } = this.state
        if (action == 1) {
            if (viewGallery.includes(gallery)) viewGallery = viewGallery.filter(gal => gal != gallery)
            else viewGallery.push(gallery)
            this.setState({ viewGallery: viewGallery, isChanged: true })
        }
        else if (action == 2) {
            if (editGallery.includes(gallery)) {
                editGallery = editGallery.filter(gal => gal != gallery)
                viewGallery = viewGallery.filter(gal => gal != gallery)
            }
            else {
                editGallery.push(gallery)
                if (!viewGallery.includes(gallery)) viewGallery.push(gallery)
            }
            this.setState({ editGallery: editGallery, viewGallery: viewGallery, isChanged: true })
        }
    }

    onSelectAllGallerys = (event, action) => {
        const { allWalls } = this.props
        if (action == 1) {
            this.setState({ viewGallery: event.target.checked ? allWalls.map(wall => { return wall.id }) : [], isChanged: true })
        }
        else if (action == 2) {
            this.setState({ viewGallery: event.target.checked ? allWalls.map(wall => { return wall.id }) : [], isChanged: true, editGallery: event.target.checked ? allWalls.map(wall => { return wall.id }) : [] })
        }
    }

    onCreateGallery = (event) => { this.setState({ createGallery: event.target.checked ? true : false, isChanged: true }) }

    onChangePermission = (type, action) => {
        let stateName = this.state[type];
        if (stateName == 0) this.setState({ [type]: action })
        else this.setState({ [type]: this.state[type] == action ? 0 : action })
    }

    onCancel = () => {
        if (this.state.isChanged) {
            const commonContent = window?.APP_CONTENT?.["common"] ?? null;
            SWAL_FIRE({
                title: this.state.isEdit ? commonContent?.confirm_cancel ?? "" : commonContent?.are_you_sure ?? "",
                text: this.state.isEdit ? commonContent?.discard_txt ?? "" : commonContent?.delete_prem_txt ?? "",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: this.state.isEdit ? commonContent?.cancel_it ?? "" : commonContent?.proceed ?? "",
                confirmButtonColor: this.state.isEdit ? `var(--bs-success)` : '',
            }).then((result) => {
                if (result.isConfirmed) {
                    this.props.navigate('/collaborator')
                }
            })
        }
        else this.props.navigate('/collaborator')
    }

    onSubmit = () => {
        const { emailId, viewGallery, editGallery, isAdmin, isEdit } = this.state
        const { createCollaborator, collaboratorList } = this.props
        const commonContent = window?.APP_CONTENT?.["common"] ?? null;
        if (emailId.trim().length > 0 && validateEmail(emailId)) {
            const exist = collaboratorList.length > 0 ? collaboratorList.filter(collab => collab['UserCollab.emailId'] == emailId) : []
            if (!isEdit && exist.length > 0) {
                TOAST_ERROR(commonContent?.email_exists ?? "")
            }
            else {
                if (isAdmin == false && viewGallery.length == 0 && editGallery.length == 0) TOAST_ERROR(commonContent?.select_1_gallery ?? "")
                else {
                    const payload = this.createPayload()
                    createCollaborator(payload, () => this.props.navigate('/collaborator'), isEdit ? true : null)
                }
            }

        }
        else {
            if (emailId.trim().length <= 0 || !validateEmail(emailId)) {
                this.setState({ isEmailError: true })
                TOAST_ERROR(commonContent?.valid_email ?? "")
            }
        }

    }

    createPayload = () => {
        const { isAdmin, emailId, website, shopon, email_camp, manageAccount, manageFeed, analytics, apps_more, createGallery, viewGallery, editGallery, product_catalog } = this.state
        const { allWalls } = this.props

        const payloadData = isAdmin ? {
            emailId: emailId,
            userType: 1,
        } :
            {
                emailId: emailId,
                userType: 2,
                permission: {
                    contentGallery: {
                        createGallery: createGallery ? 2 : 0,
                        viewGallery: viewGallery.length == allWalls.length ? "0" : String(viewGallery),
                        editGallery: editGallery.length == allWalls.length ? "0" : String(editGallery)
                    },
                    publish: {
                        website: website,
                        email_camp: email_camp,
                        shopon: shopon,
                    },
                    networkManager: {
                        manageAccount: manageAccount,
                        manageFeed: manageFeed
                    },
                    otherSettings: {
                        analytics: analytics == 1 || analytics == 2 ? analytics : 1,
                        apps_more: apps_more
                    },
                    product_catalog: product_catalog
                }
            }

        return payloadData
    }

    render() {
        const { isAdmin, manageAccount, manageFeed, createGallery, isEmailError, loading, emailId, isEdit, editGallery, viewGallery, product_catalog } = this.state
        const { allWalls, userData } = this.props;
        const commonContent = window?.APP_CONTENT?.["common"] ?? null;
        const pageContent = window?.APP_CONTENT?.["collab"] ?? null;
        return <>
            <TabHeading userData={userData} crumbs={[{ name: "Collaborator", link: "/home", icon: "grid-2", imgUrl: userData?.user?.picture }, { name: "Home", link: "/home" }, { name: "collaborator", link: "/collaborator" }, { name: "Create" }]}>
                <div className='d-flex flex-column flex-column-fluid full-content'>
                    <div className="container">
                        {/* <Card className="mb-xl-5 mb-4" style={{minHeight:'calc(100vh - 250px)'}}> */}
                        <Card className="mb-0" style={{ minHeight: 'calc(100vh - 135px)' }}>
                            {loading ? <Loader /> :
                                <Card.Body className="p-lg-6">
                                    {/* <div style={{ minHeight: 'calc(100vh - 330px)' }}> */}
                                    <div style={{ minHeight: 'calc(100vh - 239px)' }}>
                                        <div className='pb-3 mb-8 border-bottom'>
                                            <Row className='mb-4'>
                                                <Col xl={5} lg={6}>
                                                    <Form.Label htmlFor="basic-url">{commonContent?.email ?? ""}<strong className="text-danger">*</strong></Form.Label>
                                                    <div className="d-flex align-items-start">
                                                        <InputGroup className="mb-3 me-3" style={{ maxWidth: 600 }}>
                                                            <InputGroup.Text className='text-dark'><FontAwesomeIcon type={'solid'} icon={'user-plus'} /></InputGroup.Text>
                                                            <Form.Control size='lg' placeholder={commonContent?.enter_email ?? ""} onChange={this.onChangeMailId} value={emailId} className={isEmailError ? 'is-invalid' : ''} />
                                                            {isEmailError && <Form.Control.Feedback type="invalid" className='d-block'>{commonContent?.valid_email ?? ""}</Form.Control.Feedback>}
                                                        </InputGroup>
                                                        {userData && (userData.collaborator == '' || userData.collaborator.activeAccount == "0") ? <DropdownButton variant="outline-secondary" className='mb-3' size="lg" title={isAdmin ? commonContent?.admin ?? "" : commonContent?.manager ?? ""}>
                                                            <Dropdown.Item onClick={() => this.setState({ isAdmin: true })}>{commonContent?.admin ?? ""}</Dropdown.Item>
                                                            <Dropdown.Item onClick={() => this.setState({ isAdmin: false })}>{commonContent?.manager ?? ""}</Dropdown.Item>
                                                        </DropdownButton>
                                                            : null}
                                                    </div>
                                                </Col>
                                            </Row>
                                            <Alert variant="primary" className='border border-primary'>
                                                <Alert.Heading className='f-center alert_icon_head'>
                                                    <span className='f-center'>
                                                        {`${isAdmin ? commonContent?.admin ?? "" : commonContent?.manager ?? ""} *`}
                                                    </span>
                                                </Alert.Heading>
                                                <p className="fw-medium mb-0 fs-8 text-gray-700">{formatTemplateText(isAdmin ? pageContent?.admin_desc ?? "" : pageContent?.manager_desc ?? "", { company: COMPANY_NAME })}</p>
                                            </Alert>
                                        </div>
                                        {!isAdmin ?
                                            <>
                                                <Row>
                                                    <Col lg={4}>
                                                        <h4 className="h5"><FontAwesomeIcon icon={'image'} iconClass={'me-1 fs-6'} />{pageContent?.content_gallery ?? ""}<span className="text-danger">*</span></h4>
                                                        <p>{formatTemplateText(pageContent?.content_gallery_desc ?? "", { company: COMPANY_NAME })}</p>
                                                    </Col>
                                                    <Col lg={8}>
                                                        <Card className='mb-6 shadow-none'>
                                                            <Card.Body className='p-xxl-6'>
                                                                <div className="mb-2 d-flex flex-wrap">
                                                                    <Form.Check inline label={pageContent?.view_all_gallery ?? ""} name="vGallery" className="me-3 mb-2" type={'checkbox'} id={`inline-view`} onChange={event => this.onSelectAllGallerys(event, editGallery.length > 0 && editGallery.length == viewGallery.length ? 2 : 1)} checked={viewGallery.length == allWalls.length && allWalls.length > 0 ? true : false} />
                                                                    <Form.Check inline label={pageContent?.edit_all_gallery ?? ""} name="eGallery" className="me-3 mb-2" type={'checkbox'} id={`inline-edit`} onChange={event => this.onSelectAllGallerys(event, 2)} checked={editGallery.length == allWalls.length && allWalls.length > 0 ? true : false} />
                                                                    <Form.Check inline label={pageContent?.create_new_gallery ?? ""} name="createGallery" className="me-3 mb-2" type={'checkbox'} id={`inline-create`} onChange={this.onCreateGallery} checked={createGallery} />
                                                                </div>
                                                                <Card className='mb-0 shadow-none overflow-hidden'>
                                                                    <Card.Body className='pe-lg-4 ps-lg-6 bg-gray-100'>
                                                                        {allWalls.map(gallery => {
                                                                            return <div className='d-flex align-items-center justify-content-between py-2 border-bottom' key={gallery.id}>
                                                                                <div className="d-flex align-items-center w-100" style={{ maxWidth: 'calc(100% - 150px)' }}>
                                                                                    <div className="d-flex align-items-center w-100">
                                                                                        <ProfileImage className={'symbol symbol-38 rounded-2 flex-shrink-0 me-2 overflow-hidden'}
                                                                                            imageSize={38}
                                                                                            symbolClass={'symbol-label fs-6 bg-primary'}
                                                                                            name={gallery.name} profileUrl={''} networkId={''} />
                                                                                        <div className="overflow-hidden w-100 d-flex flex-grow-1 flex-column">
                                                                                            <p className="text-gray-dark fw-semibold mb-0 text-ellipsis fs-8">{gallery.name}</p>
                                                                                            <span className="fs-9 mb-0 text-gray-500 fw-medium text-ellipsis d-block">{gallery.totalPost} Posts</span>
                                                                                        </div>
                                                                                    </div>
                                                                                </div>
                                                                                <div key={`gallery-action-view`} className="my-2 d-flex">
                                                                                    <Form.Check reverse inline label={commonContent?.view ?? ""} name="gallery_action" className="me-3 me-xl-10" type={'checkbox'} id={`gallery-action-view`} checked={viewGallery.includes(gallery.id)} onChange={() => this.onSelectGallery(gallery.id, editGallery.includes(gallery.id) ? 2 : 1)} />
                                                                                    <Form.Check reverse inline label={commonContent?.edit ?? ""} name="gallery_action" type={'checkbox'} id={`gallery-action-edit`} onChange={event => this.onSelectGallery(gallery.id, 2)} checked={editGallery.includes(gallery.id) ? true : false} />
                                                                                </div>
                                                                            </div>
                                                                        })}
                                                                    </Card.Body>
                                                                </Card>
                                                            </Card.Body>
                                                        </Card>
                                                    </Col>
                                                </Row>
                                                <Row>
                                                    <Col lg={4}>
                                                        <h4 className="h5"><FontAwesomeIcon icon={'bag-shopping'} iconClass={'me-1 fs-6'} />{pageContent?.product_catalog ?? ""}</h4>
                                                        <p>{pageContent?.product_catalog_desc ?? ""}</p>
                                                    </Col>
                                                    <Col lg={8}>
                                                        <Card className='mb-6 shadow-none'>
                                                            <Card.Body className='p-xxl-6'>
                                                                <Table borderless responsive className='mb-0'>
                                                                    <tbody>
                                                                        <tr>
                                                                            <td className='fw-medium'>{commonContent?.products ?? ""}</td>
                                                                            <td className='text-end'>
                                                                                <div className="d-flex justify-content-end">
                                                                                    <Form.Check reverse inline label={commonContent?.view ?? ""} name="prod-view" className="me-3 me-xl-10" type={'checkbox'} id={`prod-view`} checked={product_catalog == 2 || product_catalog == 1 ? true : false} onChange={event => this.onChangePermission('product_catalog', product_catalog == 2 ? 2 : 1)} />
                                                                                    <Form.Check reverse inline label={commonContent?.edit ?? ""} name="prod-edit" type={'checkbox'} id={`prod-edit`} onChange={event => this.onChangePermission('product_catalog', 2)} checked={product_catalog == 2 ? true : false} />
                                                                                </div>
                                                                            </td>
                                                                        </tr>
                                                                    </tbody>
                                                                </Table>
                                                            </Card.Body>
                                                        </Card>
                                                    </Col>
                                                </Row>
                                                <Row>
                                                    <Col lg={4}>
                                                        <h4 className="h5"><FontAwesomeIcon icon={'paper-plane'} iconClass={'me-1 fs-6'} />{pageContent?.publish_touch ?? ""}</h4>
                                                        <p>{pageContent?.publish_touch_desc ?? ""}</p>
                                                    </Col>
                                                    <Col lg={8}>
                                                        <Card className='mb-6 shadow-none'>
                                                            <Card.Body className='p-xxl-6'>
                                                                <Table borderless responsive className='mb-0'>
                                                                    <tbody>
                                                                        {publishHeadings.map((head, index) => {
                                                                            return <tr>
                                                                                <td className='fw-medium'>{head.name}</td>
                                                                                <td className='text-end'>
                                                                                    <div key={index} className="d-flex justify-content-end">
                                                                                        <Form.Check reverse inline label={commonContent?.view ?? ""} name={head.type} className="me-3 me-xl-10" type={'checkbox'} id={head.type} checked={this.state[head.type] == 2 || this.state[head.type] == 1 ? true : false} onChange={event => this.onChangePermission(head.type, this.state[head.type] == 2 ? 2 : 1)} />
                                                                                        <Form.Check reverse inline label={commonContent?.edit ?? ""} name={head.type} type={'checkbox'} id={head.type} onChange={event => this.onChangePermission(head.type, 2)} checked={this.state[head.type] == 2 ? true : false} />
                                                                                    </div>
                                                                                </td>
                                                                            </tr>
                                                                        })}
                                                                    </tbody>
                                                                </Table>
                                                            </Card.Body>
                                                        </Card>
                                                    </Col>
                                                </Row>
                                                <Row>
                                                    <Col lg={4}>
                                                        <h4 className="h5"><FontAwesomeIcon icon={'grid-2-plus'} iconClass={'me-1 fs-6'} />{pageContent?.network_manager ?? ""}</h4>
                                                        <p>{pageContent?.network_manager_desc ?? ""}</p>
                                                    </Col>
                                                    <Col lg={8}>
                                                        <Card className='mb-6 shadow-none'>
                                                            <Card.Body className='p-xxl-6'>
                                                                <Table borderless responsive className='mb-0'>
                                                                    <tbody>
                                                                        <tr>
                                                                            <td className='fw-medium'>{pageContent?.add_manage_acc ?? ""}</td>
                                                                            <td className='text-end'>
                                                                                <div key={`socialAccount-action-view`} className="d-flex justify-content-end">
                                                                                    <Form.Check reverse inline label={commonContent?.view ?? ""} name="socialAccount_action" className="me-3 me-xl-10" type={'checkbox'} id={`socialAccount-action-view`} checked={manageAccount == 2 || manageAccount == 1} onChange={() => this.onChangePermission('manageAccount', manageAccount == 2 ? 2 : 1)} />
                                                                                    <Form.Check reverse inline label={commonContent?.edit ?? ""} name="socialAccount_action" type={'checkbox'} id={`socialAccount-action-edit`} onChange={event => this.onChangePermission('manageAccount', 2)} checked={manageAccount == 2} />
                                                                                </div>
                                                                            </td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td className='fw-medium'>{pageContent?.create_feed ?? ""}</td>
                                                                            <td className='text-end'>
                                                                                <div key={`connection-action-view`} className="d-flex justify-content-end">
                                                                                    <Form.Check reverse inline label={commonContent?.view ?? ""} name="connection_action" className="me-3 me-xl-10" type={'checkbox'} id={`connection-action-view`} checked={manageFeed == 1 || manageFeed == 2} onChange={() => this.onChangePermission('manageFeed', manageFeed == 2 ? 2 : 1)} />
                                                                                    <Form.Check reverse inline label={commonContent?.edit ?? ""} name="connection_action" type={'checkbox'} id={`connection-action-edit`} onChange={event => this.onChangePermission('manageFeed', 2)} checked={manageFeed == 2} />
                                                                                </div>
                                                                            </td>
                                                                        </tr>
                                                                    </tbody>
                                                                </Table>
                                                            </Card.Body>
                                                        </Card>
                                                    </Col>
                                                </Row>
                                                <Row>
                                                    <Col lg={4}>
                                                        <h4 className="h5"><FontAwesomeIcon icon={'grid-2'} iconClass={'me-1 fs-6'} />{pageContent?.additional_features ?? ""}</h4>
                                                        <p>{pageContent?.additional_features_desc ?? ""}</p>
                                                    </Col>
                                                    <Col lg={8}>
                                                        <Card className='mb-6 shadow-none'>
                                                            <Card.Body className='p-xxl-6'>
                                                                <Table borderless responsive className='mb-0'>
                                                                    <tbody>
                                                                        {featureHeadings.map((head, index) => {
                                                                            return <tr>
                                                                                <td className='fw-medium'>{head.name}</td>
                                                                                <td className='text-end'>
                                                                                    <div key={index} className="d-flex justify-content-end">
                                                                                        <Form.Check reverse inline label={commonContent?.view ?? ""} name={head.type} className="me-3 me-xl-10" type={'checkbox'} id={head.type} checked={this.state[head.type] == 2 || this.state[head.type] == 1 || head.type == 'analytics' ? true : false} onChange={event => this.onChangePermission(head.type, 2)} disabled={head.type == "analytics"} />
                                                                                        <Form.Check reverse inline label={commonContent?.edit ?? ""} name={head.type} type={'checkbox'} id={head.type} onChange={event => this.onChangePermission(head.type, 2)} checked={this.state[head.type] == 2 ? true : false} />
                                                                                    </div>
                                                                                </td>
                                                                            </tr>
                                                                        })}
                                                                    </tbody>
                                                                </Table>
                                                            </Card.Body>
                                                        </Card>
                                                    </Col>
                                                </Row>
                                            </>
                                            : null}
                                    </div>
                                    <div className="d-flex justify-content-end align-items-center flex-wrap mt-3 mt-lg-4">
                                        <div className="block-center">
                                            <Button className="btn btn-secondary btn-min-w me-3" onClick={this.onCancel}>{commonContent?.cancel ?? ""}</Button>
                                            <Button variant={'primary'} onClick={this.onSubmit}>{isEdit ? commonContent?.update ?? "" : commonContent?.create ?? ""} {isAdmin ? commonContent?.admin ?? '' : commonContent?.manager ?? ''}</Button>
                                        </div>
                                    </div>
                                </Card.Body>}
                        </Card>
                    </div>
                </div>
            </TabHeading>
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        userData: state.authorized?.userData?.user_data,
        collaboratorList: state.collaborator.collaboratorList,
    }
}

const mapDispatchToProps = dispatch => ({
    createCollaborator: (payload, callback, isEdit) => dispatch(addCollaborator(payload, callback, isEdit)),
})


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ManageCollaborator))